"use client";

import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";
import { ExternalLink, Github, ChevronLeft, ChevronRight } from "lucide-react";

interface Project {
    id: number;
    title: string;
    description: string;
    tags: string[];
    image: string;
    githubUrl?: string;
    liveUrl?: string;
}

const projects: Project[] = [
    {
        id: 1,
        title: "E-Commerce Dashboard",
        description:
            "A comprehensive dashboard for managing online stores, featuring real-time analytics, inventory management, and order processing.",
        tags: ["Next.js", "TypeScript", "Tailwind CSS", "Prisma"],
        image: "https://images.unsplash.com/photo-1460925895917-afdab827c52f?q=80&w=2426&auto=format&fit=crop",
        githubUrl: "https://github.com",
        liveUrl: "https://example.com",
    },
    {
        id: 2,
        title: "AI Chat Application",
        description:
            "A real-time chat application powered by LLMs, supporting multiple concurrent conversations and context retention.",
        tags: ["React", "Node.js", "Socket.io", "OpenAI API"],
        image: "https://images.unsplash.com/photo-1587560699334-cc4da63c2409?q=80&w=2532&auto=format&fit=crop",
        githubUrl: "https://github.com",
        liveUrl: "https://example.com",
    },
    {
        id: 3,
        title: "Portfolio Website",
        description:
            "A modern, responsive portfolio website featuring smooth animations, dark mode, and a clean minimalist design.",
        tags: ["Next.js", "Framer Motion", "Tailwind CSS"],
        image: "https://images.unsplash.com/photo-1507238691740-187a5b1d37b8?q=80&w=2555&auto=format&fit=crop",
        githubUrl: "https://github.com",
        liveUrl: "https://example.com",
    },
    {
        id: 4,
        title: "Task Management Tool",
        description:
            "A collaborative task management tool with drag-and-drop kanban boards, team assignments, and deadline tracking.",
        tags: ["Vue.js", "Firebase", "Pinia"],
        image: "https://images.unsplash.com/photo-1484480974693-6ca0a78fb36b?q=80&w=2672&auto=format&fit=crop",
        githubUrl: "https://github.com",
        liveUrl: "https://example.com",
    },
];

const ProjectsSection = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const [direction, setDirection] = useState(0);

    const nextProject = () => {
        setDirection(1);
        setCurrentIndex((prev) => (prev + 1) % projects.length);
    };

    const prevProject = () => {
        setDirection(-1);
        setCurrentIndex((prev) => (prev - 1 + projects.length) % projects.length);
    };

    const variants = {
        enter: (direction: number) => ({
            x: direction > 0 ? 1000 : -1000,
            opacity: 0,
        }),
        center: {
            zIndex: 1,
            x: 0,
            opacity: 1,
        },
        exit: (direction: number) => ({
            zIndex: 0,
            x: direction < 0 ? 1000 : -1000,
            opacity: 0,
        }),
    };

    return (
        <section id="projects" className="py-20 px-6 md:px-12 bg-[#111111] min-h-screen flex flex-col justify-center overflow-hidden">
            <div className="max-w-7xl mx-auto w-full">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ duration: 0.5 }}
                    className="mb-12 text-center"
                >
                    <h2 className="text-3xl md:text-5xl font-bold text-white mb-4">
                        Featured Projects
                    </h2>
                    <div className="w-24 h-1 bg-blue-500 rounded-full mx-auto" />
                </motion.div>

                <div className="relative h-[600px] md:h-[500px] w-full flex items-center justify-center">
                    {/* Navigation Buttons */}
                    <button
                        onClick={prevProject}
                        className="absolute left-0 z-20 p-3 rounded-full bg-white/10 hover:bg-white/20 text-white backdrop-blur-sm transition-all hover:scale-110 hidden md:block"
                    >
                        <ChevronLeft size={32} />
                    </button>

                    <button
                        onClick={nextProject}
                        className="absolute right-0 z-20 p-3 rounded-full bg-white/10 hover:bg-white/20 text-white backdrop-blur-sm transition-all hover:scale-110 hidden md:block"
                    >
                        <ChevronRight size={32} />
                    </button>

                    {/* Mobile Navigation (Bottom) */}
                    <div className="absolute -bottom-12 flex gap-4 md:hidden z-20">
                        <button
                            onClick={prevProject}
                            className="p-3 rounded-full bg-white/10 hover:bg-white/20 text-white backdrop-blur-sm transition-all"
                        >
                            <ChevronLeft size={24} />
                        </button>
                        <button
                            onClick={nextProject}
                            className="p-3 rounded-full bg-white/10 hover:bg-white/20 text-white backdrop-blur-sm transition-all"
                        >
                            <ChevronRight size={24} />
                        </button>
                    </div>

                    <div className="w-full max-w-5xl h-full relative">
                        <AnimatePresence initial={false} custom={direction}>
                            <motion.div
                                key={currentIndex}
                                custom={direction}
                                variants={variants}
                                initial="enter"
                                animate="center"
                                exit="exit"
                                transition={{
                                    x: { type: "spring", stiffness: 300, damping: 30 },
                                    opacity: { duration: 0.2 },
                                }}
                                className="absolute w-full h-full"
                            >
                                <div className="flex flex-col md:flex-row h-full bg-[#1a1a1a] rounded-2xl overflow-hidden border border-gray-800 shadow-2xl">
                                    {/* Image Section */}
                                    <div className="w-full md:w-1/2 h-1/2 md:h-full relative overflow-hidden group">
                                        <img
                                            src={projects[currentIndex].image}
                                            alt={projects[currentIndex].title}
                                            className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                                        />
                                        <div className="absolute inset-0 bg-black/30 group-hover:bg-black/10 transition-colors duration-300" />
                                    </div>

                                    {/* Content Section */}
                                    <div className="w-full md:w-1/2 p-8 md:p-12 flex flex-col justify-center">
                                        <h3 className="text-2xl md:text-4xl font-bold text-white mb-4">
                                            {projects[currentIndex].title}
                                        </h3>
                                        <p className="text-gray-400 text-lg mb-8 leading-relaxed">
                                            {projects[currentIndex].description}
                                        </p>

                                        <div className="flex flex-wrap gap-3 mb-8">
                                            {projects[currentIndex].tags.map((tag) => (
                                                <span
                                                    key={tag}
                                                    className="px-4 py-2 text-sm font-medium text-blue-400 bg-blue-500/10 rounded-full border border-blue-500/20"
                                                >
                                                    {tag}
                                                </span>
                                            ))}
                                        </div>

                                        <div className="flex gap-4 mt-auto">
                                            {projects[currentIndex].githubUrl && (
                                                <Link
                                                    href={projects[currentIndex].githubUrl}
                                                    target="_blank"
                                                    className="flex items-center gap-2 px-6 py-3 bg-white text-black rounded-full font-semibold hover:bg-gray-200 transition-colors"
                                                >
                                                    <Github size={20} />
                                                    Code
                                                </Link>
                                            )}
                                            {projects[currentIndex].liveUrl && (
                                                <Link
                                                    href={projects[currentIndex].liveUrl}
                                                    target="_blank"
                                                    className="flex items-center gap-2 px-6 py-3 bg-transparent border border-white text-white rounded-full font-semibold hover:bg-white/10 transition-colors"
                                                >
                                                    <ExternalLink size={20} />
                                                    Live Demo
                                                </Link>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </motion.div>
                        </AnimatePresence>
                    </div>
                </div>

                {/* Indicators */}
                <div className="flex justify-center gap-2 mt-16 md:mt-8">
                    {projects.map((_, index) => (
                        <button
                            key={index}
                            onClick={() => {
                                setDirection(index > currentIndex ? 1 : -1);
                                setCurrentIndex(index);
                            }}
                            className={`w-3 h-3 rounded-full transition-all duration-300 ${index === currentIndex ? "bg-blue-500 w-8" : "bg-gray-600 hover:bg-gray-500"
                                }`}
                        />
                    ))}
                </div>
            </div>
        </section>
    );
};

export default ProjectsSection;
